.form{
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 420px;
  min-height: 420px;
  padding: 50px;
  margin: 5% auto;
  gap: 20px;

  background: #FFFFFF;
  box-shadow: 0 1px 2px rgba(27, 58, 89, 0.25);
  border-radius: 8px;
}

.wrapperImage{
  position: absolute;
  top: -50px;
}

.inputWrapper{
  position: relative;
  display: flex;
  flex-direction: column;
  max-width: 313px;
  width: 100%;
}
.input{
  padding: 15px;
  background: #FFFFFF;
  outline: none;
  border: 1px solid rgba(27, 58, 89, 0.4);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 6px;
}

.error{
  position: absolute;
  color: red;
  font-weight: bold;
  top: 85%;
  left: 50%;
  transform: translateX(-50%);
}
